<!--  -->
<template>
  <div>
  <h1>当前用户 {{ user }}</h1>
    <el-calendar v-model="value">
      <template slot="dateCell"
                slot-scope="{date, data}">

        <div class="date-item"
             @click="dateClick(date,data)">
          {{ data.day.split('-').slice(1).join('-') }}
          <!-- {{ data.isSelected ? '✔️' : ''}} -->
          <div class="things">{{data|filterEvent(that)}}</div>
          <span class="things">{{data|filterEventMore(that)}}</span>
        </div>
      </template>
    </el-calendar>
    <div class="tool-wrap">
      <el-button type="primary"
                 v-if="form.date"
                 @click="dialogFormVisible=true"> add event</el-button>
      <span class="tip">Slide to see more</span>
    </div>
    <div class="thing-box">
      <div class="thing-content">
        <div class="ting-wrap"
             v-for="(item,index) in todayEventList"
             :key="index">
          <!-- v-if="flg" -->
          <p class="today">Date:{{item.date}}</p>
          <p class="name">To do event:{{item.name}}</p>
          <p class="detail">event detail:{{item.desc}}</p>
          <p class="time">Remind time:{{item.time}}</p>
          <el-button @click="deleTing(index)"
                     class="dele-button"
                     type="danger">delete event</el-button>
        </div>
      </div>
    </div>

    <el-dialog title="new event"
               :visible.sync="dialogFormVisible">
      <el-form :model="form"
               label-position="left"
               label-width="120px">
        <el-form-item label="event name">
          <el-input v-model="form.name"
                    autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="event detail">
          <el-input v-model="form.detail"
                    autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="remind time">
          <el-time-picker v-model="form.time"
                          format="HH:mm"
                          :picker-options="{
      selectableRange: '00:00:00 - 23:59:00'
    }"
                          placeholder="choose remind time">
          </el-time-picker>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisible = false">cancel</el-button>
        <el-button type="primary"
                   @click="addEvent()">confirm</el-button>
      </div>
    </el-dialog>

    <!-- 闹钟提示弹窗 -->
    <el-dialog title="clock"
               :visible.sync="dialogVisible"
               width="30%">
      <div>{{clockEvent.name}}</div>
      <div>{{clockEvent.desc}}</div>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false">cancel</el-button>
        <el-button type="primary"
                   @click="dialogVisible = false">confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  components: {},

  data () {
    return {
      that: this,
      dialogFormVisible: false,
      dialogVisible: false,
      value: new Date(),
      form: {
        date: "",
        time: "",
        name: "",
        detail: "",
      },
       user: "",
      flg: 0,//标志当前点击日期是否已经存在事件
      eventlist: [],//事件列表
      todayEventList: [],
      // {
      //   date: 121212,
      //   name: "12121",
      //   desc: 12121,
      //   time: 1212,
      //   id: 4545
      // },//当前日期的数据
      timer: null,
      currentMonth: "",
      clockEventList: [],//闹钟事件列表
      clockEvent: {},//闹钟事件
      hasSelectDay: false,//当前鼠标是否有选中日期
      // currentDay:""//用于记录当前鼠标选中的日期
    }
  },
  filters: {
    // 展示该日期的事件
    filterEvent (data, that) {
      let eventName = ""
      that.eventlist.forEach(val => {
        if (val.date == data.day) {
          eventName = val.calendarBeanList[0].name
        }
      })
      return eventName
    },
    filterEventMore (data, that) {
      let isMore = ""
      that.eventlist.forEach(val => {
        if (val.date == data.day) {
          if (val.calendarBeanList.length > 1) {
            isMore = "..."
          }
        }
      })
      return isMore
    }
  },
  mounted: function () {
  console.log("check status");
  this.checklogin()
 },
  created () {
  
    this.formatDate(this.value)
    this.getData()
    this.$nextTick(() => {
      // 点击前一个月
      let prevBtn = document.querySelector(
        ".el-calendar__button-group .el-button-group>button:nth-child(1)"
      );
      prevBtn.addEventListener("click", () => {
        this.form = this.$options.data().form//清空当前选中日期
        console.log(this.value);
        this.formatDate(this.value)
        this.getData()

      })

      //点击下一个月
      let nextBtn = document.querySelector(
        ".el-calendar__button-group .el-button-group>button:nth-child(3)"
      );
      nextBtn.addEventListener("click", () => {
        this.form = this.$options.data().form//清空当前选中日期
        console.log(this.value);
        this.formatDate(this.value)
        this.getData()

      })
    })
  },
  methods: {
    addEvent () {
      // 格式化成时间
      this.form.time = this.$moment(this.form.time).format('hh:mm');
      console.log(this.form.time, "this.form.time")
      this.$axios({
        method: "get",
        url: "/exapi/addEvent?name=" + this.form.name + "&desc=" + this.form.detail + "&date=" + this.form.date + "&time=" + this.form.time,
        data: {}
      }).then((res) => {        console.log(res, 78787);
        this.$message.success("添加成功")
        this.form = this.$options.data().form
        // this.todayEventList.push({

        // })
        this.getData()
        this.dialogFormVisible = false
      })
    },
    dateClick (date, data) {

      this.todayEventList = []

      this.form = this.$options.data().form
      this.form.date = data.day
      // 先找出是否已经存在事件  flg 用于弹窗展示或者是其他
      this.flg = 0
      this.eventlist.forEach(val => {
        if (val.date == data.day) {
          this.todayEventList = val.calendarBeanList
          console.log(this.todayEventList, 23232)
          this.flg = 1
        }
      })
      if (!this.flg) {
        this.todayEventList = []
        console.log(date, 333, data, 121212)
        this.dialogFormVisible = true

      }

    },
    deleTing (index) {
      // console.log(this.current)
      this.$axios({
        method: "get",
        url: "/exapi/removeEvent?id=" + this.todayEventList[index].id,
        data: {}
      }).then((res) => {        console.log(res, 454545);
        if (res) {
          this.$message.success("删除成功")
          this.flg = 0
          // this.todayEventList.splice(index, 1)
          this.getData()
        } else {
          this.$message.waring("删除失败")
        }
      })
    },
    formatDate (date) {
      let a = date.toLocaleDateString()
      a = a.split('/')
      this.currentMonth = a[0] + '-' + a[1]
      // this.currentMonth=this.$moment(date).format("MM-DD")
      console.log(this.currentMonth, 9696)
    },
    getData () {
       clearInterval(this.timer);
       this.timer=null
      this.todayEventList = []
      let that1 = this
      console.log(5555)
      this.$axios({
        method: "get",
        url: "/exapi/getList?yearAndMonth=" + this.currentMonth,
        data: {}
      }).then((res) => {        console.log(res, 454545);
        this.eventlist = res.data
        // 获取今日日期 用于时钟
        let todate = this.$moment(new Date()).format('YYYY-MM-DD')
        // console.log(todate, "todate")
        this.eventlist.forEach(val => {
          // console.log(val.date, "val.date")
          if (val.date == todate) {
            // 如果今天又事件 就需要把事件先存起来
            this.clockEventList = val.calendarBeanList.map(val => {
              return { ...val }
            })
            let clockEventListTem = val.calendarBeanList.map(val => {
              return { ...val }
            })
            // 闹钟比对时间
            this.timer = setInterval(() => {
              let nowtime = that1.$moment(new Date()).format('hh:mm');
              clockEventListTem.forEach((val, index) => {
                that1.clockEvent = val
                if (nowtime == (val.time)) {
                  that1.dialogVisible = true
                  clockEventListTem.splice(index, 1)
                }
              })

              console.log(nowtime)
            }, 5000);
          }
        })
        console.log(this.clockEvent, "this.clockEvent")


      })

    },
    checklogin(){

    this.$axios.get('/phpapi/check.php')
      .then((response)=>{
        console.log(response);


        console.log("response.data");

        if(response.data.code==1){

        this.user=response.data.user.firstname;
        }else{
console.log("@34234");
        console.log(response.data);
        window.location.href = "http://127.0.0.1:8890/loginpage.html";
        }
      }).catch(function (error) {
        console.log(error);
      });

    }

  }
}

</script>
<style >
.tool-wrap {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 30px;
}
.tip {
  margin-left: 30px;

  display: block;
  color: #409eff;
}
.thing-box {
  width: 100vw;
  overflow-x: scroll;
}
.thing-content {
  width: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.ting-wrap {
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 30px;
}
.dele-button {
  margin-left: 400px;
}
p {
  margin: 10px;
}
/deep/.el-form-item__content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.things {
  color: red;
  font-size: 14px;
  margin-top: 16px;
}
.date-item {
  width: 100%;
  height: 100%;
}
</style>
